.editable {
    width: 100%;
}

.editable-display {
    /* background-color: #f8f8f8; */
    padding: 25px;
    text-align: center;
    align-items: center;
    justify-content: center;
    margin: auto;
    display: flex;
}

.editable-display:hover {
  background-color: #f1f1f1;
}

.editable-edit {
    display: flex;
}

.editable-edit input {
  border-radius: 3px;
  outline: none;
  font-size: 1rem;
}

.editable-edit-footer button {
  outline: none;
  border: none;
  color: #fff;
  transition: 200ms;
  width: 80%;
  padding: 8px 18px;
}

.editable-edit-footer button:hover {
  background-color: #9cc9bf;
}

.editable-edit svg {
  cursor: pointer;
}
